import { request } from "@/utils/request";
import { REQUEST_SUCCEEDED_CODE } from "@/common/constants";
/**
 * signInLottery 签到领取vip界面
 * @description 签到领取vip界面
 * @Author: 穆兰
 * @Date: 2022/1/7
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/9
 */
export default {
    data() {
        return {
            signInfo: {
                signInDays: 0,
                signInToday: false,
                getGiftToday: false,
            },
        };
    },
    methods: {
        /**
         *@description 点击返回按钮
         */
        onNarLeftClick() {
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         * @description 点击签到按钮
         */
        async onSignButtonClick() {
            if (this.signInfo.signInDays >= 7) {
                if (this.signInfo.getGiftToday) {
                    uni.showToast({
                        title: "奖品已领取",
                        icon: "none",
                    });
                }
                else {
                    getSignGift();
                }
                return;
            }
            if (this.signInfo.signInToday) {
                return;
            }
            else {
                let res = await signIn();
                if (res) {
                    this.signInfo = await getSignInfo();
                }
            }
        },
    },
    async mounted() {
        this.signInfo = await getSignInfo();
        console.log("this.signInfo", this.signInfo);
    },
};
/**
 * @description 签到
 * @returns {Promise<Boolean>}
 */
async function signIn() {
    let res = await request({
        data: {
            method: "PUT",
            group: "signIn",
            action: `signIn`,
            data: {},
            header: {
                "content-type": "application/x-www-form-urlencoded",
            },
        },
    });
    if (res.data.code === REQUEST_SUCCEEDED_CODE) {
        uni.showToast({
            title: "签到成功",
            icon: "success",
        });
        return true;
    }
    else {
        console.log("signInfail", res.data);
        return false;
    }
}
/**
 * @description 签到信息
 * @returns {Promise<SignInfo>}
 */
async function getSignInfo() {
    let res = await request({
        data: {
            method: "GET",
            group: "signIn",
            action: `info`,
            data: {},
            header: {
                "content-type": "application/x-www-form-urlencoded",
            },
        },
    });
    if (res.data.code === REQUEST_SUCCEEDED_CODE) {
        let signInfo = res.data.data;
        return signInfo;
    }
    else {
        console.log("signInfoFail", res.data);
        return {
            signInDays: 0,
            signInToday: false,
            getGiftToday: false,
        };
    }
}
/**
 * @description 获取签到礼品
 */
async function getSignGift() {
    let res = await request({
        data: {
            method: "POST",
            group: "signIn",
            action: `getGift`,
            data: {},
            header: {
                "content-type": "application/x-www-form-urlencoded",
            },
        },
    });
    if (res.data.code === REQUEST_SUCCEEDED_CODE) {
        uni.showToast({
            title: "奖品领取成功",
        });
        console.log("getGiftSuccess", res.data);
    }
    else {
        console.log("getGiftFail", res.data);
    }
}
